    `
    function fn(a, b) {
        console.log(this, a, b, arguments); 
    }
    fn.apply({a: 1}, [1, 2]); // {a: 1} 1 2 [1, 2]
    `,
    `
    let float32 = Int8Array.of.apply(Float32Array.prototype.constructor, [5,6,1,9]); // 修改构造函数
